.img {
	position: relative;
	margin-bottom: 26px;
	-webkit-box-shadow: 0 26px 12px -12px rgba(0, 0, 0, 0.25);
	        box-shadow: 0 26px 12px -12px rgba(0, 0, 0, 0.25);
}

.img:before {
	content: '';
	pointer-events: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: red;
	border-radius: 5px;
	background-image: url('http://htmlcolorcodes.com/assets/images/html-color-codes-color-tutorials-hero-00e10b1f.jpg');
	background-size: cover;
	pointer-events: none;
	-webkit-filter: grayscale(50%);
	        filter: grayscale(50%);
}

.img__content {
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-position: center;
	border-radius: 5px;
	pointer-events: none;
}



